'use client';

import { useState, useEffect } from 'react';
import { useSearchParams } from 'next/navigation';
import Navbar from '@/components/Navbar';
import Footer from '@/components/Footer';
import SearchBar from '@/components/SearchBar';
import ListingCard from '@/components/ListingCard';
import Loading from '@/components/Loading';
import Error from '@/components/Error';
import { fetchListings } from '@/lib/api';
import { Listing } from '@/types';

export default function ListingsPage() {
  const searchParams = useSearchParams();
  const [listings, setListings] = useState<Listing[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [page, setPage] = useState(1);
  const [hasMore, setHasMore] = useState(true);

  const city = searchParams.get('city') || '';
  const minPrice = searchParams.get('minPrice') || '';
  const maxPrice = searchParams.get('maxPrice') || '';
  const roomType = searchParams.get('roomType') || '';

  useEffect(() => {
    loadListings();
  }, [searchParams]);

  const loadListings = async () => {
    setLoading(true);
    setError(null);

    try {
      const response = await fetchListings({
        city: city || undefined,
        minPrice: minPrice ? parseInt(minPrice) : undefined,
        maxPrice: maxPrice ? parseInt(maxPrice) : undefined,
        roomType: roomType || undefined,
        page: 1,
        limit: 12,
      });

      setListings(response.listings);
      setHasMore(response.meta.page < response.meta.totalPages);
      setPage(1);
    } catch (err: any) {
      setError(err.message || 'Failed to load listings');
    } finally {
      setLoading(false);
    }
  };

  const loadMore = async () => {
    if (loading || !hasMore) return;

    setLoading(true);
    try {
      const response = await fetchListings({
        city: city || undefined,
        minPrice: minPrice ? parseInt(minPrice) : undefined,
        maxPrice: maxPrice ? parseInt(maxPrice) : undefined,
        roomType: roomType || undefined,
        page: page + 1,
        limit: 12,
      });

      setListings((prev) => [...prev, ...response.listings]);
      setHasMore(response.meta.page < response.meta.totalPages);
      setPage((prev) => prev + 1);
    } catch (err: any) {
      setError(err.message || 'Failed to load more listings');
    } finally {
      setLoading(false);
    }
  };

  if (loading && listings.length === 0) {
    return (
      <div className="flex min-h-screen flex-col">
        <Navbar />
        <Loading />
        <Footer />
      </div>
    );
  }

  if (error && listings.length === 0) {
    return (
      <div className="flex min-h-screen flex-col">
        <Navbar />
        <Error message={error} onRetry={loadListings} />
        <Footer />
      </div>
    );
  }

  return (
    <div className="flex min-h-screen flex-col">
      <Navbar />

      <main className="flex-1 bg-gray-50">
        <div className="bg-white py-8">
          <div className="mx-auto max-w-7xl px-4 sm:px-6 lg:px-8">
            <h1 className="mb-6 text-3xl font-bold text-gray-900">
              Find Your Perfect Accommodation
            </h1>
            <SearchBar
              initialCity={city}
              initialBudget={{
                min: minPrice ? parseInt(minPrice) : 0,
                max: maxPrice ? parseInt(maxPrice) : 5000,
              }}
              initialRoomType={roomType as 'single' | 'shared' | 'entire' | undefined}
            />
          </div>
        </div>

        <div className="mx-auto max-w-7xl px-4 py-8 sm:px-6 lg:px-8">
          {error && (
            <div className="mb-6 rounded-lg bg-red-50 p-4">
              <p className="text-sm text-red-800">{error}</p>
            </div>
          )}

          {listings.length === 0 ? (
            <div className="py-16 text-center">
              <p className="text-lg text-gray-600">No listings found.</p>
              <p className="mt-2 text-gray-500">
                Try adjusting your search filters.
              </p>
            </div>
          ) : (
            <>
              <div className="mb-6">
                <p className="text-gray-600">
                  Found {listings.length} {listings.length === 1 ? 'listing' : 'listings'}
                </p>
              </div>

              <div className="grid grid-cols-1 gap-6 md:grid-cols-2 lg:grid-cols-3">
                {listings.map((listing) => (
                  <ListingCard key={listing.id} listing={listing} />
                ))}
              </div>

              {hasMore && (
                <div className="mt-8 text-center">
                  <button
                    onClick={loadMore}
                    disabled={loading}
                    className="rounded-lg bg-blue-600 px-8 py-3 font-medium text-white transition-colors hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50"
                  >
                    {loading ? 'Loading...' : 'Load More'}
                  </button>
                </div>
              )}
            </>
          )}
        </div>
      </main>

      <Footer />
    </div>
  );
}

