import Image from 'next/image';
import Link from 'next/link';
import { Listing } from '@/types';

interface ListingCardProps {
  listing: Listing;
}

export default function ListingCard({ listing }: ListingCardProps) {
  const roomTypeLabels = {
    single: 'Single Room',
    shared: 'Shared Room',
    entire: 'Entire Place',
  };

  return (
    <Link href={`/listings/${listing.id}`} className="group">
      <div className="overflow-hidden rounded-xl bg-white shadow-sm transition-shadow hover:shadow-lg">
        <div className="relative h-64 w-full overflow-hidden bg-gray-200">
          {listing.images && listing.images.length > 0 ? (
            <Image
              src={listing.images[0]}
              alt={listing.title}
              fill
              className="object-cover transition-transform group-hover:scale-105"
              sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
            />
          ) : (
            <div className="flex h-full items-center justify-center bg-gray-100">
              <svg
                className="h-12 w-12 text-gray-400"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z"
                />
              </svg>
            </div>
          )}
        </div>

        <div className="p-4">
          <div className="mb-2 flex items-start justify-between">
            <div className="flex-1">
              <h3 className="text-lg font-semibold text-gray-900 line-clamp-1">
                {listing.title}
              </h3>
              <p className="text-sm text-gray-600">{listing.location}</p>
            </div>
            <div className="ml-2 text-right">
              <p className="text-lg font-bold text-gray-900">${listing.price}</p>
              <p className="text-xs text-gray-500">per month</p>
            </div>
          </div>

          <div className="mb-3">
            <span className="inline-block rounded-full bg-blue-100 px-3 py-1 text-xs font-medium text-blue-800">
              {roomTypeLabels[listing.roomType]}
            </span>
          </div>

          <p className="line-clamp-2 text-sm text-gray-600">{listing.description}</p>

          {listing.amenities && listing.amenities.length > 0 && (
            <div className="mt-3 flex flex-wrap gap-2">
              {listing.amenities.slice(0, 3).map((amenity, index) => (
                <span
                  key={index}
                  className="text-xs text-gray-500"
                >
                  • {amenity}
                </span>
              ))}
              {listing.amenities.length > 3 && (
                <span className="text-xs text-gray-500">
                  +{listing.amenities.length - 3} more
                </span>
              )}
            </div>
          )}
        </div>
      </div>
    </Link>
  );
}

