'use client';

import { useState, FormEvent } from 'react';
import { useRouter } from 'next/navigation';

interface SearchBarProps {
  initialCity?: string;
  initialBudget?: { min: number; max: number };
  initialRoomType?: 'single' | 'shared' | 'entire';
  onSearch?: (filters: {
    city: string;
    budget: { min: number; max: number };
    roomType: string;
  }) => void;
}

export default function SearchBar({
  initialCity = '',
  initialBudget = { min: 0, max: 5000 },
  initialRoomType,
  onSearch,
}: SearchBarProps) {
  const router = useRouter();
  const [city, setCity] = useState(initialCity);
  const [minPrice, setMinPrice] = useState(initialBudget.min.toString());
  const [maxPrice, setMaxPrice] = useState(initialBudget.max.toString());
  const [roomType, setRoomType] = useState(initialRoomType || '');

  const handleSubmit = (e: FormEvent) => {
    e.preventDefault();
    const filters = {
      city,
      budget: {
        min: parseInt(minPrice) || 0,
        max: parseInt(maxPrice) || 5000,
      },
      roomType,
    };

    if (onSearch) {
      onSearch(filters as any);
    } else {
      const params = new URLSearchParams();
      if (city) params.append('city', city);
      if (minPrice) params.append('minPrice', minPrice);
      if (maxPrice) params.append('maxPrice', maxPrice);
      if (roomType) params.append('roomType', roomType);
      router.push(`/listings?${params.toString()}`);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="w-full">
      <div className="rounded-2xl bg-white p-4 shadow-lg md:p-6">
        <div className="grid grid-cols-1 gap-4 md:grid-cols-4">
          <div className="md:col-span-1">
            <label htmlFor="city" className="mb-2 block text-sm font-medium text-gray-700">
              Location
            </label>
            <input
              id="city"
              type="text"
              value={city}
              onChange={(e) => setCity(e.target.value)}
              placeholder="City or area"
              className="w-full rounded-lg border border-gray-300 px-4 py-2 text-sm focus:border-blue-500 focus:outline-none focus:ring-2 focus:ring-blue-200"
            />
          </div>

          <div>
            <label htmlFor="roomType" className="mb-2 block text-sm font-medium text-gray-700">
              Room Type
            </label>
            <select
              id="roomType"
              value={roomType}
              onChange={(e) => setRoomType(e.target.value)}
              className="w-full rounded-lg border border-gray-300 px-4 py-2 text-sm focus:border-blue-500 focus:outline-none focus:ring-2 focus:ring-blue-200"
            >
              <option value="">All Types</option>
              <option value="single">Single Room</option>
              <option value="shared">Shared Room</option>
              <option value="entire">Entire Place</option>
            </select>
          </div>

          <div>
            <label htmlFor="minPrice" className="mb-2 block text-sm font-medium text-gray-700">
              Min Price
            </label>
            <input
              id="minPrice"
              type="number"
              value={minPrice}
              onChange={(e) => setMinPrice(e.target.value)}
              placeholder="0"
              min="0"
              className="w-full rounded-lg border border-gray-300 px-4 py-2 text-sm focus:border-blue-500 focus:outline-none focus:ring-2 focus:ring-blue-200"
            />
          </div>

          <div>
            <label htmlFor="maxPrice" className="mb-2 block text-sm font-medium text-gray-700">
              Max Price
            </label>
            <input
              id="maxPrice"
              type="number"
              value={maxPrice}
              onChange={(e) => setMaxPrice(e.target.value)}
              placeholder="5000"
              min="0"
              className="w-full rounded-lg border border-gray-300 px-4 py-2 text-sm focus:border-blue-500 focus:outline-none focus:ring-2 focus:ring-blue-200"
            />
          </div>
        </div>

        <div className="mt-4 flex justify-end">
          <button
            type="submit"
            className="rounded-lg bg-blue-600 px-8 py-2 font-medium text-white transition-colors hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2"
          >
            Search
          </button>
        </div>
      </div>
    </form>
  );
}

